.slider {
  padding-top: 130px;
  padding-bottom: 130px;
}

.slider h3 {
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  color: $dark;
  text-align: center;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
}

.slider p {
  font-size: 16px;
  line-height: 21px;
  color: $dark;
  text-align: center;
  margin: 0;
  padding: 0;
}

.slider__wrapper {
  max-width: 1170px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 60px;
  margin-top: 60px;
}

.slider__item img {
  margin-bottom: 25px;
}

.slider__item p {
  font-size: 16px;
  line-height: 21px;
  color: $dark;
  text-align: center;
}

.slider__item span {
  display: block;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  color: $dark;
  text-align: center;
  margin-top: 15px;
}

.slider__dots {
  display: flex;
  margin: 0 auto;
  max-width: 48px;
  justify-content: space-between;
}

.slider__dots span {
  font-size: 16px;
  line-height: 21px;
  color: $dark;
}
